
import XCircle from 'vux/src/components/x-circle/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'

import axios from 'axios'
import * as util from '../api/common'

export default {
components: {
XCircle, Cell, Group
},
data () {
return {
dataInfo: {
cover_charse: [
{
id: 0,
order_sn: '',
buyer_id: 0,
amortize_time: 0,
month_account: 0.00,
refund_account: 0.00,
over_cover_charse: 0,
cover_charse: 0.00,
status: 0,
end_time: 0,
created_at: '',
updated_at: ''
}
],
user_account: '8666.66',    // 可用额度
cover_charse_amount: '7.75'   
}
}
},
mounted () {
this.getData()
},
methods: {
getData () {
let params = new URLSearchParams()
var self = this
params.append('bill_id', util.getQueryParam('id'))
var instance = axios.create({ baseURL: util.baseURL })

instance.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
instance.post('buyer/center/covercharseList', params).then(res => {
console.log(res.data.data)
if (res.data) {
self.dataInfo = res.data.data
}
})
},
dateFormat (date) {
return new Date(date * 1000).Format('yyyy-MM-dd ') 
}
}
}



